.info__close-button {
  width: 32px;
  height: 32px;
  background-image: url(../../../images/form_close.svg);
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  right: -40px;
  top: -40px;
  background-color: transparent;
  border: none;
 }
 
 .info__close-button:hover {
   cursor: pointer;
   opacity: 0.6;
 }
 
 @media screen and (min-width: 320px) and (max-width: 767px) {
   .info__close-button {
     background-image: url(../../../images/form_close_mobile.svg);
     width: 20px;
     height: 20px;
     top: -36px;
     right: 0;
   }
 }